  color: #6a6a6a;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */ }

body {
  font-family: "RobotoRegular", sans-serif;
  font-size: 16px;
  line-height: 1.875;
