/*  оформление мобильной версии меню и гамбургера */
.button_over {
  position: relative;
  width: 60px;
  height: 50px;
  display: none; }

.button_container {
  position: absolute;
  top: 0;
  left: 2px;
  bottom: 0;
  right: 0;
  width: 50px;
  height: 50px;
  cursor: pointer;
  z-index: 100;
  transition: opacity .25s ease; }

.button_container:hover {
  opacity: .7; }

.button_container.active .top {
  transform: translateY(11px) translateX(0) rotate(45deg);
  background: #FFF; }

.button_container.active .middle {
  opacity: 0;
  background: #FFF; }

.button_container.active .bottom {
  transform: translateY(-11px) translateX(0) rotate(-45deg);
  background: #FFF;
  transform: translateY(-8px) translateX(0) rotate(-45deg); }

.button_container span {
  background: #2c2c2c;
  border: none;
  height: 3px;
  width: 80%;
  position: absolute;
  top: 10px;
  left: 5%;
  transition: all .35s ease;
  cursor: pointer; }

.button_container span:nth-of-type(2) {
  top: 19px; }

.button_container span:nth-of-type(3) {
  top: 28px; }

.overlay {
  position: fixed;
  z-index: 1000;
  background: #474747;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .35s, visibility .35s, height .35s;
  overflow: hidden; }

.overlay.open {
  opacity: .9;
  visibility: visible;
  height: 100%; }

.overlay.open li {
  animation: fadeInRight .5s ease forwards;
  animation-delay: .35s; }

.overlay.open li:nth-of-type(2) {
  animation-delay: .4s; }

.overlay.open li:nth-of-type(3) {
  animation-delay: .45s; }

.overlay.open li:nth-of-type(4) {
  animation-delay: .50s; }

.overlay nav {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-weight: 400;
  text-align: center; }

.overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  height: 100%; }

.overlay ul li {
  display: block;
  position: relative;
  opacity: 0; }

.overlay ul li a {
  display: block;
  position: relative;
  color: #fff;
  text-transform: uppercase;
  font-size: 1.625rem;
  padding: 20px 0;
  text-decoration: none;
  overflow: hidden;
  transition: .35s; }

.overlay ul li a:hover {
  color: #1a9bdc; }

.overlay ul li a:hover:after,
.overlay ul li a:focus:after,
.overlay ul li a:active:after {
  width: 100%; }

.overlay ul li a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  transform: translateX(-50%);
  height: 3px;
  background: #1a9bdc;
  transition: .35s; }

@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }
  100% {
    opacity: 1;
    left: 0; } }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }
  100% {
    opacity: 1;
    left: 0; } }

