  background-color: #0087be;
  color: #fff; }

.accordion-item.active .icon:after {
  width: 0; }

.accordion-item .heading {
  display: block;
  text-transform: capitalize;
  text-decoration: none;
  font-family: "Montserrat-Medium", sans-serif;
  /*  // font-size: $size; */
