  font-family: "Font Awesome 5 Free";
  content: "\f0da";
  font-weight: 600;
  color: #0087be;
  border: 0; }

.mfp-arrow-right:before {
  border: 0; }

.mfp-arrow:before, .mfp-arrow:after {
  top: 50%;
  margin-top: 0; }

