.forms-call {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  border: 0;
  overflow: hidden; }

.forms-call label {
  margin: 10px 0;
  display: block; }

.input_form {
  display: block;
  width: 100%;
  margin: 0 auto 15px;
  height: 40px;
  /*  // font-size: $size; */
  font-size: 1.25rem;
  border: 0;
  outline: 0;
  line-height: 40px;
  color: #a9a9a9;
  border-bottom: 1px solid #a9a9a9;
  text-indent: 10px;
  background-color: #171717;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.input_form:active,
.input_form:focus,
.input_form:hover {
  border: 0;
  outline: 0;
  border-bottom: 1px solid #a9a9a9; }

.input_textarea {
  min-height: 100px;
  resize: none;
  overflow: hidden; }

.forms-call .header_button {
  border: 0;
  outline: 0;
  padding: 11px 25px; }

/* стилизую форму всплывашку для обратной связи */
.form-callback {
  max-width: 750px;
  overflow: hidden;
  padding: 20px;
  position: relative;
  /* 	background-color:$color_button; */
  transition: all 0.5s else;
  margin: auto; }

/*
стилизую кнопки закрытия формы */
.form-callback .mfp-close {
  position: absolute;
  top: 4px;
  right: 4px;
  opacity: 1;
  width: 30px;
  height: 30px;
  line-height: 30px;
  /*  // font-size: $size; */
  font-size: 1.875rem;
  border-radius: 50%;
  background-color: #95989A;
  border: 1px solid #fff;
  transition: all .75s ease; }

.form-callback .mfp-close:hover {
  color: #1a1a1a; }

.forms-calldecor {
  position: relative; }

.forms-calldecor .success {
  color: #fff;
  background-color: #fff;
  position: absolute;
  z-index: 1;
  left: 0;
  z-index: 1100;
  top: -100%;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  padding-right: 20px;
  padding-left: 20px;
  transition: all 3.0s ease; }

.forms-calldecor .success.active {
  top: 0;
  opacity: 1; }

.forms-calldecor .success p {
  text-transform: uppercase;
  display: flex;
  align-self: center;
  color: #fff;
  font-size: 20px;
  margin: auto;
  text-align: center; }

/* долюно работать анимация всплывашки */
/* overlay at start */
.mfp-fade.mfp-bg {
  opacity: 0;
  transition: all 0.615s ease-out; }

/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready {
  opacity: 0.8; }

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing {
  opacity: 0; }

/* content at start */
.mfp-fade.mfp-wrap .mfp-content {
  opacity: 0; }

/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
  opacity: 1;
  transition: all 0.615s ease-out;
  transform: translateY(1%); }

/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
  opacity: 0;
  transform: translateY(-120%); }

